import React from 'react'
import './_pizza-block.scss'

import { useDispatch, useSelector } from 'react-redux'
import { Link } from 'react-router-dom'
import { addProduct } from '../../redux/cart/Slice'
import { RootState } from '../../redux/store'

export const typeNames = ['Тонкое', 'Традиционное']

export type pizzaItem = {
	id: string
	imageUrl: string
	title: string
	price: number
	sizes: number[]
	types: number[]
}

const PizzaItem: React.FC<pizzaItem> = ({ id, imageUrl, title, price, sizes, types }) => {
	const [activeType, setActiveType] = React.useState(0)
	const [activeSize, setActiveSize] = React.useState(0)

	const cartItem = useSelector((state: RootState) =>
		state.cart.products.find((obj) => obj.id === id)
	)
	const addedCount = cartItem ? cartItem.count : 0

	const dispatch = useDispatch()

	const onclickAdd = () => {
		const product = {
			id,
			title,
			price,
			imageUrl,
			type: types[activeType],
			size: sizes[activeSize],
			count: 0,
		}
		dispatch(addProduct(product))
	}

	return (
		<div className='pizza-block'>
			<Link to={`pizza/${id}`} className='pizza-block__image'>
				<div className='pizza-block__image'>
					<img width='260' height='260' src={imageUrl} alt='Pizza' />
				</div>
				<h4 className='pizza-block__title'>{title}</h4>
			</Link>
			<div className='pizza-block__selector'>
				<ul>
					{types.map((typeId, i) => {
						return (
							<li
								key={i}
								onClick={() => setActiveType(typeId)}
								className={activeType === i ? 'active' : ''}
							>
								{typeNames[typeId]}
							</li>
						)
					})}
				</ul>
				<ul>
					{sizes.map((size, i) => {
						return (
							<li
								key={i}
								onClick={() => setActiveSize(i)}
								className={activeSize === i ? 'active' : ''}
							>
								{size} см.
							</li>
						)
					})}
				</ul>
			</div>
			<div className='pizza-block__bottom'>
				<div className='pizza-block__price'>от {price} ₽</div>
				<button onClick={onclickAdd} className='button button--outline button--add'>
					<svg
						width='12'
						height='12'
						viewBox='0 0 12 12'
						fill='none'
						xmlns='http://www.w3.org/2000/svg'
					>
						<path
							d='M10.8 4.8H7.2V1.2C7.2 0.5373 6.6627 0 6 0C5.3373 0 4.8 0.5373 4.8 1.2V4.8H1.2C0.5373 4.8 0 5.3373 0 6C0 6.6627 0.5373 7.2 1.2 7.2H4.8V10.8C4.8 11.4627 5.3373 12 6 12C6.6627 12 7.2 11.4627 7.2 10.8V7.2H10.8C11.4627 7.2 12 6.6627 12 6C12 5.3373 11.4627 4.8 10.8 4.8Z'
							fill='white'
						></path>
					</svg>
					<span>Добавить</span>
					{addedCount > 0 && <i>{addedCount}</i>}
				</button>
			</div>
		</div>
	)
}

export default PizzaItem
